	export default {
		onShow: function() {
			var res = this.globalIslogin();
			if (!res) {
				console.log('未登录')
				uni.showModal({
					title: '请登录',
					content: "请登录",
					success: function() {
						uni.navigateTo({
							url: "/pages/login/login"
						});
					},
					fail:function(){
						console.log('fail')
					},
					icon:'loading',
				})
			}else{
				console.log('yes')
			}
		},
}
